.login {
      margin-top: 100px;
    
    &__container {
        display: flex;
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        width: 500px;
        justify-content: center;
    }
    
    &__wrapper {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    
    &__input {
        background: #FFFFFF;
        border: 1px solid #C7CCD0;
        border-radius: 8px;
        padding-left: 16px;
        padding-top: 11px;
        padding-bottom: 11px;
        width: 361px;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        transition: all 0.4s ease;
        &:hover {
            transform: scale(1.05);
        }
    }
    
    &__lang {
        outline: 0;
        border: 0;
        background: none;
        padding: 4px 8px;
        position: absolute;
        top: 35px;
        left: 48%;
    }

    &__btn {
        border: none;
        outline: none;
        background: linear-gradient(180deg, #E93B77 0%, #DA1F63 100%);
        border-radius: 8px;
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        padding-top: 15px;
        padding-bottom: 15px;
        line-height: 140%;
        width: 381px;
        text-align: center;
        color: #FFFFFF;
    }
}

.container {
    max-width: 1240px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
}