.modal {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    
    
    &__inner {
        width: 500px;
        background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
        background-size: 400% 400%;
        animation: Toxirrrr 3s ease infinite;
        padding: 20px;
        border-radius: 10px;
    }
    @keyframes Toxirrrr {
        0% {
            background-position: 0% 50%;
        }
        50% {
            background-position: 100% 50%;
        }
        100% {
            background-position: 0% 50%;
        }
    }
    .modal__header {
        margin: 0;
    }
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        &div {
            
        }
    }
    
    &__btn {
        border: none;
        outline: none;
        background: linear-gradient(180deg, #E93B77 0%, #DA1F63 100%);
        color: white;
        margin-left: 350px;
    }
}